
import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import MyGreatPlace from './MyGreatPlace';
import axios from "axios"
var urlbackend = require("../../env/env")();
export default class Tracking extends Component {


    static defaultProps = {
        center: {
            lat: 36.1,
            lng: 10.1
        },
        zoom: 8
    };



    constructor(props) {
        super(props);
        this.state = {
            listPosition:[]
        }
    }
    componentDidMount(){

        console.log(this.props.locationto)
        const config = {
            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Headers': 'Authorization',
              "x-access-token": localStorage.getItem('token'),
              'Authorization': 'Bearer '+localStorage.getItem('token'),
            },
          };
    
        axios
          .get(urlbackend + "/"+this.props.fortracking+"/location/"+this.props.locationto, config)
          .then((response) => {
            console.log("this respon locationto", response.data.data);
         this.setState({ listPosition: response.data.data });
          })
          .catch((e) => {
            console.log(e);
          });
    }

    render() {
        return (

                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyB82mLJM4AnpZmSQmnvXjXsUETGidCj3Yo" }}
                    defaultCenter={this.props.center}
                    defaultZoom={this.props.zoom}
                >

                     {
                        this.state.listPosition.map((e)=>(
                            <MyGreatPlace key={e.id} lat={e.latitude} lng={e.longitude} text={'A'} /* Kreyser Avrora */ />

                          ))
                    } 


                </GoogleMapReact>
            
        );
    }
}